	module.exports = {"pathContext":{"yourName":"Cheung Pui Nei","projectName":"Desire","wordDescription":"Different yet the same\nCollisions and rags passing through the distant Ganges\n\nChaotic soul breaks down and reconstruct \nWhat’s the final puzzle as broken pieces imbody to you and me?\n\nThe installation radiate a cold and latent violence. At times, disconcerting beauty emerges. The inherent visual seductiveness, along with the conciseness of the exhibitions, further complicates the reception of their manifold layers of meaning. By contesting the division between the realm of memory and the realm of experience, absorbed the tradition of remembrance art into daily practice. This personal follow-up and revival of a past tradition is important as an act of meditation.\nHer collected, altered and own works are being confronted as aesthetically resilient, thematically interrelated material for memory and projection. The possible seems true and the truth exists, but it has many faces, as Hanna Arendt cites from Franz Kafka.","mapCoOrdinates":"(-80, -80)","gifs":"Guillaume.gif","slug":"cheung-pui-nei","coordinates":{"x":0.1,"y":0.1}}}

